export default {
	data(){
		return {
			labelList:[
				{name:'全部',number:25,type:'all'},
				{name:'好评',number:23,type:'good'},
				{name:'中评',number:1,type:'secondary'},
				{name:'差评',number:1,type:'poor'},
				{name:'有图',number:12,type:'img'},
				{name:'视频',number:2,type:'video'},
				{name:'追加',number:2,type:'append'}
			],
			labelIndex:0, // 当前评论类型下标
			ratingsList:[],
			videoContext:null, // 播放器对象
			videoDirection:0, // 全屏方向
			showFullscreenBtn:true, // 是否全屏
			videoSrc:'',
			showVideo: true // 小程序端隐藏
		}
	},
	onLoad(){
		try {
			const comments = uni.getStorageSync('comments');
			if (comments) {
				this.ratingsList = comments
				console.log(this.ratingsList)
			}
		} catch (e) {
			// error
		}
		// 判断是否是小程序端，如果是就隐藏

		this.showVideo = false

	},
	onReady(){ 
		// 创建并返回 video 上下文 videoContext 对象。
		this.videoContext = uni.createVideoContext('myVideo')
	},
	methods:{
		// 切换选中评论
		loadRatings(index){
			this.labelIndex = index;
		},
		// 放大图片
		showBigImg(src,srclist){
			uni.previewImage({
				current:src,
				urls: srclist
			});
		},
		// 播放视频
		playVideo(path) {
			// console.log(path)
			this.videoSrc = path
			// 全屏播放
			this.$nextTick(() => {
				this.videoContext.requestFullScreen({
					direction:0
				});
			})

			this.showVideo = true

		},
		videoControl(e){
			// 判断是否处于全屏状态
			if(e.detail.fullScreen){
				// 自动播放
				this.videoContext.play();
			}else{
				// 停止播放
				// this.videoContext.pause();	
				this.videoPause()
			}
		},
		// 退出全屏后，停止播放视频
		videoPause(){
			this.videoSrc = ''

			this.showVideo = false

		}, 
		videoError(e){
			console.log(e)
		}
		
	}
}
